body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.photo-card {
  width: 300px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.info {
  text-align: center;
  padding: 10px;
}

.load-more {
  cursor: pointer;
  background-color: #00f;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

